import React from "react";
import ReactDOM from "react-dom";
import { Routes, Route, HashRouter } from "react-router-dom";

import Home from "./views/Home";
import Blog from "./views/Blog";
import Post from "./views/Post";
import About from "./views/About";
// import OpenCoreSanityChecker from "./ocsc/index";
import PageNotFound from "./views/404";

import Navbar from "./components/Navbar/Navbar";
import Footer from "./components/Footer";
// import CookieConsent from "./components/CookieConsent";

import "./assets/scss/main.scss";

const App = () => {
    const [isDarkMode, setDarkMode] = React.useState(localStorage.getItem("dark") ?? false);

    const isMain = ((path = window.location.hash.toLowerCase()) => (!path.includes("#/opencore") && !path.includes("#/dialgo")))();

    // const isMain = (path = window.location.hash.toLowerCase()) => {
    //     // console.log(path)
    //     return((!path.includes("#/opencore") && !path.includes("#/dialgo")));
    // };

    return(<>
        {isMain && 
            <Navbar
                isDarkMode={isDarkMode}
                setDarkMode={setDarkMode}
            />
        }
            <Routes>
                <Route exact path="/" element={<Home />}/>
                <Route exact path="/blog" element={<Blog />} />
                <Route exact path="/blog/:url" element={<Post isDarkMode={isDarkMode} />} />
                <Route exact path="/about" element={<About />} />
                {/* <Route exact path="/opencore" element={<OpenCoreSanityChecker/>} /> */}
                <Route exact path="*" element={<PageNotFound />} />
            </Routes>
        {isMain && <Footer />}
        {/* {!localStorage.getItem("cookie_consent") && <CookieConsent />} */}
    </>);
}

ReactDOM.render(<HashRouter><App /></HashRouter>, document.getElementById("root"));