import React from "react";
import { Link } from "react-router-dom";

// import LangToggle from "./LangToggle";
import ModeToggle from "./ModeToggle";

export default function Navbar({isDarkMode, setDarkMode}){
    const navbarToggleRef = React.useRef(null);
    const linksRef = React.useRef(null);

    const toggleNavbar = () => {
        const isMobile = window.innerWidth<800;

        let isOpen = false;
        
        const toggleAll = () => {
            navbarToggleRef.current.classList.toggle("active");
            linksRef.current.classList.toggle("active");
            document.documentElement.classList.toggle("noscroll");
        }

        if(isMobile){
            toggleAll();
            isOpen = true;

            window.addEventListener("resize", e => {
                if(e.target.innerWidth>isMobile && isOpen){
                    toggleAll();
                    isOpen = false;
                }
            })
        }
    }

    return(
        <header className="main-navbar">
            {/* <LangToggle 
                langData={langData}
                setLangData={setLangData}
            /> */}
            <div className="navbar-mobile">
                <div 
                    ref={navbarToggleRef} 
                    id="navbar-toggle" 
                    onClick={toggleNavbar}
                >
                    <span />
                </div>
            </div>
            <div ref={linksRef} className="links">
                <Link to="/" onClick={toggleNavbar}>@velickovicdj</Link>
                <Link to="/blog" onClick={toggleNavbar}>Blog</Link>
                <a href="/files/CV_VELICKOVICDJ.pdf" onClick={toggleNavbar}>CV</a>
                <Link to="/about" onClick={toggleNavbar}>About</Link>
            </div>
            <ModeToggle
                isDarkMode={isDarkMode}
                setDarkMode={setDarkMode}
            />
        </header>
    );
}